// Listen on a specific host via the HOST environment variab

bridge.send("VKWebAppInit");

class Example extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      activePanel: 'mainPanel',
      activeView: 'mainView',
      switches: [false, false, false, false, false, false, false, false],
      active: [false, false, false, false, false, false, false, false],
      current: 0,
    };
    var self = this;
    this.doNewState = function(){
      self.setState((state)=>{
        let active = state.active;
        active[self.state.current] = true;
        active[self.state.current==0?7:self.state.current-1] = false;
        return {
          active,
        }
      });
      bridge.send("VKWebAppFlashGetInfo").then((answer)=> {
        console.log(answer)
        if (self.state.switches[self.state.current] && answer.level==0) {
          bridge.send("VKWebAppTapticSelectionChanged", {});
          bridge.send("VKWebAppFlashSetLevel", {"level": 1});
        } else if (!self.state.switches[self.state.current] && answer.level==1){
          bridge.send("VKWebAppTapticSelectionChanged", {});
          bridge.send("VKWebAppFlashSetLevel", {"level": 0});
        }
        self.state.current = (self.state.current+1)%8;
      })
    }
    let timer = setInterval(this.doNewState, 1000);
  }

  // doNewState() {
  //   if (this.state.switches[this.state.current]){
  //     bridge.send("VKWebAppFlashSetLevel", {"level": 1});
  //   } else {
  //     bridge.send("VKWebAppFlashSetLevel", {"level": 0});
  //   }
  //   this.state.current = (this.state.current+1)%8;
  // }

  didSelectionChanged(id) {
    bridge.send("VKWebAppTapticSelectionChanged", {});
    this.setState(state => {
      const switches = state.switches;
      switches[id] = !switches[id];
      return {
        switches,
      }
    });
  }

  render() {
    return (
        <AppRoot>
          <Root activeView={this.state.activeView}>
            <View id={"mainView"} activePanel={this.state.activePanel}>
              <Panel id={"mainPanel"}>
                <PanelHeader>ASAPIt Flashbit</PanelHeader>
                <Group style={{padding:"10px"}}>
                  <Text>
                    Внимание на инструкцию! Данный MiniApp позволит Вам делать настоящий бит вашим
                    фонариком! <br/> Поставьте в желаемое положение 8 переключателей снизу и наслаждайтесь
                  </Text>
                </Group>
                <Group>
                  <HorizontalScroll>
                    {/*<SimpleCell style={{backgroundColor: this.state.active[0]?"rgb(55,118,240)":"rgba(0,0,0,0)"}} after={<Switch onChange={()=>this.didSelectionChanged(0)}/>}>1 секунда</SimpleCell>*/}
                    {/*<SimpleCell style={{backgroundColor: this.state.active[1]?"rgb(55,118,240)":"rgba(0,0,0,0)"}} after={<Switch onChange={()=>this.didSelectionChanged(1)}/>}>2 секунда</SimpleCell>*/}
                    {/*<SimpleCell style={{backgroundColor: this.state.active[2]?"rgb(55,118,240)":"rgba(0,0,0,0)"}} after={<Switch onChange={()=>this.didSelectionChanged(2)}/>}>3 секунда</SimpleCell>*/}
                    {/*<SimpleCell style={{backgroundColor: this.state.active[3]?"rgb(55,118,240)":"rgba(0,0,0,0)"}} after={<Switch onChange={()=>this.didSelectionChanged(3)}/>}>4 секунда</SimpleCell>*/}
                    {/*<SimpleCell style={{backgroundColor: this.state.active[4]?"rgb(55,118,240)":"rgba(0,0,0,0)"}} after={<Switch onChange={()=>this.didSelectionChanged(4)}/>}>5 секунда</SimpleCell>*/}
                    {/*<SimpleCell style={{backgroundColor: this.state.active[5]?"rgb(55,118,240)":"rgba(0,0,0,0)"}} after={<Switch onChange={()=>this.didSelectionChanged(5)}/>}>6 секунда</SimpleCell>*/}
                    {/*<SimpleCell style={{backgroundColor: this.state.active[6]?"rgb(55,118,240)":"rgba(0,0,0,0)"}} after={<Switch onChange={()=>this.didSelectionChanged(6)}/>}>7 секунда</SimpleCell>*/}
                    {/*<SimpleCell style={{backgroundColor: this.state.active[7]?"rgb(55,118,240)":"rgba(0,0,0,0)"}} after={<Switch onChange={()=>this.didSelectionChanged(7)}/>}>8 секунда</SimpleCell>*/}
                    <HorizontalCell size={"m"}><Switch style={{transition:"background-color 0.5s ease",marginLeft:"3px",marginRight:"3px",backgroundColor: this.state.active[0]?"rgb(55,118,240)":"rgba(0,0,0,0)",borderRadius:"16px",borderWidth:"20px", padding:"10px", display:"block"}} onChange={()=>this.didSelectionChanged(0)}/>
                    <Switch style={{transition:"background-color 0.5s ease",marginLeft:"3px",marginRight:"3px",backgroundColor: this.state.active[1]?"rgb(55,118,240)":"rgba(0,0,0,0)",borderRadius:"16px",borderWidth:"20px", padding:"10px", display:"block"}} onChange={()=>this.didSelectionChanged(1)}/>
                    <Switch style={{transition:"background-color 0.5s ease",marginLeft:"3px",marginRight:"3px",backgroundColor: this.state.active[2]?"rgb(55,118,240)":"rgba(0,0,0,0)",borderRadius:"16px",borderWidth:"20px", padding:"10px", display:"block"}} onChange={()=>this.didSelectionChanged(2)}/>
                    <Switch style={{transition:"background-color 0.5s ease",marginLeft:"3px",marginRight:"3px",backgroundColor: this.state.active[3]?"rgb(55,118,240)":"rgba(0,0,0,0)",borderRadius:"16px",borderWidth:"20px", padding:"10px", display:"block"}} onChange={()=>this.didSelectionChanged(3)}/>
                    <Switch style={{transition:"background-color 0.5s ease",marginLeft:"3px",marginRight:"3px",backgroundColor: this.state.active[4]?"rgb(55,118,240)":"rgba(0,0,0,0)",borderRadius:"16px",borderWidth:"20px", padding:"10px", display:"block"}} onChange={()=>this.didSelectionChanged(4)}/>
                    <Switch style={{transition:"background-color 0.5s ease",marginLeft:"3px",marginRight:"3px",backgroundColor: this.state.active[5]?"rgb(55,118,240)":"rgba(0,0,0,0)",borderRadius:"16px",borderWidth:"20px", padding:"10px", display:"block"}} onChange={()=>this.didSelectionChanged(5)}/>
                    <Switch style={{transition:"background-color 0.5s ease",marginLeft:"3px",marginRight:"3px",backgroundColor: this.state.active[6]?"rgb(55,118,240)":"rgba(0,0,0,0)",borderRadius:"16px",borderWidth:"20px", padding:"10px", display:"block"}} onChange={()=>this.didSelectionChanged(6)}/>
                      <Switch style={{transition:"background-color 0.5s ease",marginLeft:"3px",marginRight:"3px",backgroundColor: this.state.active[7]?"rgb(55,118,240)":"rgba(0,0,0,0)",borderRadius:"16px",borderWidth:"20px", padding:"10px", display:"block"}} onChange={()=>this.didSelectionChanged(7)}/></HorizontalCell>
                  </HorizontalScroll>
                </Group>
              </Panel>
            </View>

          </Root>
        </AppRoot>
    );
  };
};

ReactDOM.render(
    <ConfigProvider>
      <AdaptivityProvider>
        <Example />
      </AdaptivityProvider>
    </ConfigProvider>,
    document.getElementById("root")
);